body {
    width: 100%;
    margin: 0px 0px;
    padding: 0px 0px;
}

.bg {
    width: 100%;
    height: 100%;
    text-align: center;
    position: relative;
    opacity: 80%;
    font-family: Chalkboard;
    color: aliceblue;
}

.pic1 {
    background: url("pic2.jpeg") center;
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
}

.pic2 {
    background: url("pic.jpeg") center;
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
}

.pic3 {
    background: url("pic3.jpeg") center;
    background-size: 100%;
    background-attachment: fixed;
    background-repeat: no-repeat;
}

.title_box {
    width: 100%;
    position: absolute;
    top: 50%;

}

.title {
    font-size: 30px;
    letter-spacing: 8px;
    text-transform: uppercase;
    border: white 1px solid;
    padding: 40px 10px;
}

.name {
    padding-left: 10px;
    font-family: Chalkboard;
}

.menu {
    padding: 0;
    margin: 0;
    width: 100%;
    height: auto;
    position: fixed;
    background-color: #1f1f1f;
    opacity: 90%;
    color: white;
    z-index: 10;
}

.txtBox {
    opacity: 60%;
    background: #fafafa;
    height: 70%;
    width: 50%;
    position: relative;
    left: 25%;
    top: 15%;
}

.sec1 {
    margin: 50px 0px;
    font-family: Times;
    position: relative;
    text-align: center;
    letter-spacing: 3px;
}

h1 {
    letter-spacing: 3px;
}

.sec2 {
    padding: 50px 0px;
    font-family: Times;
    position: relative;
    text-align: center;
    background-color: #2f2f2f;
    opacity: 90%;
    color: white;
}

.code {
    width: 80%;
    position: relative;
    left: 9%;
    text-align: left;
    padding: 5px 1%;
    background-color: #f0f0f0;
    border: 1px solid #e1e4e5;
    border-radius: 5px;
    opacity: 90%;
    color: black;
}

.menu ul {
    width: 90%;
    list-style: none;
    display: inline;
}

.menu ul li {
    float:right;
    padding: 20px 20px;
    margin: 0px 0px;
    text-align: center;
}

.menu ul li a {
    text-decoration: none;
    color: white;
}

.menu ul li:hover {
    color: cornsilk;
    background: darkgray;
}

iframe {
    width: 80%;
    margin: 0 auto;
}

footer .info {
    margin-bottom: 2em;
    font-size: 0.8em;
    text-align: center;
}

.fter ul {
    padding: 0;
    margin: 0;
    margin-top: 1em;
    list-style: none;
    text-align: center;
}

.fter ul li {
    display: inline-block;
    padding: 0;
    margin: 0;
}

.fter ul li a {
    text-decoration: none;
    color: white;
}

.fter a {
    padding: 0 10px;
    text-decoration: none;
}

.container_flex {
    display: flex;
    color: black;
    text-align: center;
    z-index: 2;
    font-family: 'Times New Roman', Times, serif;
}

.flex_4 {
    opacity: 100%;
    flex: 4;
}

.flex_6 {
    margin: 10px 10px;
    flex: 6;
    opacity: 100%;
}